body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

select, button, a {
  font-family: 'Open Sans', sans-serif;
}

/* Buttons ---------------- */
button {
  height: 32px;
  font-size: 14px;
  font-weight: 600;
  color: #326ADF;
  border: 2px solid #326ADF;
  box-shadow: none;
  outline: none;
  cursor: pointer;
  background: white;
  margin: 0px;
}

button:not(.nav-button) {
  border-radius: 6px;
}

button.small {
  height: 24px;
  font-size: 11px;
  border-radius: 4px;
  border-width: 1px;
  border-style: solid
}

button:disabled {
  opacity: 0.5;
  cursor: default;
}

a {
  text-decoration: none;
  color: inherit;
}
.alert {
  font-size: 12px;
  padding: 4px 8px;
  margin: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.alert p {
  margin-right: 8px;
}

.alert a {
  text-decoration: underline;
}
.footer {
  font-size: 11px;
  margin: 8px 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.footer a {
  margin: 0px 4px;
}
.container {
  width: 320px;
}
.nav {
  margin-bottom: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid;
  border-radius: 6px;
  box-sizing: border-box;
}

.nav button {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0px !important;
}

.nav button {
  border-width: 0px 2px 0px 0px;
  border-radius: 0px;
}

.nav button:first-child {
  border-radius: 4px 0px 0px 4px;
}

.nav button:last-child {
  border-right: none;
  border-radius: 0px 4px 4px 0px;
}

/* ---------------- Color Schemes ---------------- */
.App .nav button.selected {
  color: white;
  background: #326ADF;
}

.App.night-mode .nav button.selected {
  color: black;
  background: white;
}
