          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 14px;
  font-weight: 600;
  height: 32px;
  padding: 0px 6px;
  margin-bottom: 8px;
  border: 2px solid;
  border-radius: 4px;
  box-sizing: border-box;
}

.slider label {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.slider .control,
.slider .control-value {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 2 1;
          flex: 2 1;
}

.slider .control input {
  margin: 0px 4px;
}

.slider .control-value {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.toolbar {
  margin-bottom: 8px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.toolbar select,
.toolbar button {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.toolbar button {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 4px;
  padding: 0px;
}

.toolbar button:not(:first-child) {
