}
.select {
  position: relative;
}

.select select {
  width: 100%;
  height: 32px;
  font-size: 14px;
  font-weight: 600;
  border-width: 2px;
  border-style: solid;
  border-radius: 4px;
  padding: 4px 6px;
  margin: 0px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.select .caret {
  position: absolute;
  right: 8px;
  top: 11px;
}

/* ---------------- Color Schemes ---------------- */
.App .select select {
  background: white;
  border-color:#326ADF;
  color: #326ADF;
}

.App.night-mode .select select {
  background: black;
  border-color:white;
  color: white;
}
.radius {
  height: 18px;
  width: 18px;
  border: 2px solid #F5B400;
  border-radius: 50%;
  position: relative;
}

.radius .center {
  background-color: #F5B400;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  position: absolute;
  top: 7px;
  right: 7px;
}

.radius .line {
  background-color: #F5B400;
  width: 8px;
  height: 2px;
  position: absolute;
  top: 8px;
  right: 0px;
}

.radius.white {
  border-color: white;
}

.radius.white .center {
  background-color: white;
}

.radius.white .line {
  background-color: white;
}

.radius.black {
  border-color: black;
}

.radius.black .center {
  background-color: black;
}

.radius.black .line {
  background-color: black;
}
.canvas {
    height: 320px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
}

.bubble {
    height: 100px;
    width: 100px;
    font-size: 10px;
    color: transparent;
    padding: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    border-radius: 50%;
    border: 2px solid;
    position: absolute;
    box-sizing: border-box;
    overflow: hidden;
    -webkit-transition: all 0.6s ease-in-out;
    transition: all 0.6s ease-in-out;
}

.bubble.focused {
    width: 320px;
    height: 320px;
    font-size: 20px;
    font-weight: 600;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    z-index: 1000;
}
.spinner {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
