}
.place {
  padding: 10px;
}

.place .name {
  text-align: center;
  margin-bottom: 4px;
}

.place .detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-align-items: center;
          align-items: center;
  font-size: 14px;
