/* ---------------- App Color Scheme ---------------- */
.App {
  background: white;
  color: #326ADF;
}

.App button {
  background: white;
  color: #326ADF;
  border-color: #326ADF;
}

.App button.selected {
  background: #326ADF;
  color: white;
}

.App a {
  color: #326ADF;
}

.App input {
  background: white;
  border: 1px solid #326ADF;
}

